import { createCustomElement } from "@servicenow/ui-core";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import { actions, actionHandlers } from "./actions";
import { view } from "./view";

/**
 * The Quantity component renders a dropdown from which the user can select an item which represents the number of units of items
 * the user wants to order/request.The default type is 'choice'.
 *
 * ```jsx
 * <sn-catalog-quantity
 * 		value={...}
 * 		items={...}
 * 		visible={...}
 *	    label={...}
 * 		/>
 * ```
 *
 * @seismicElement sn-catalog-quantity
 * @summary Quantity dropdown for catalog item
 */

createCustomElement("sn-catalog-quantity", {
	renderer: {
		type: snabbdom
	},
	view,
	properties: {
		/**
		 * Value of the selected quantity
		 *
		 * @type {string}
		 */
		value: {
			default: "1"
		},
		/**
		 * Choice items to choose quantity count
		 *
		 * @type {Array.<object>}
		 */
		items: {
			default: [
				{
					value: "1",
					displayValue: "1"
				},
				{
					value: "2",
					displayValue: "2"
				},
				{
					value: "3",
					displayValue: "3"
				},
				{
					value: "4",
					displayValue: "4"
				},
				{
					value: "5",
					displayValue: "5"
				}
			]
		},
		/**
		 * Whether to show quantity or not
		 *
		 * @type {boolean}
		 */
		visible: {
			default: true,
			schema: { type: "boolean" }
		},
		/**
		 * Label for quantity component
		 *
		 * @type {string}
		 */
		label: {
			default: "Quantity"
		}
	},
	actions,
	actionHandlers,
	dispatches: {
		/**
		 * Dispatched when catalog item quantity is updated
		 *
		 * @type {{}}
		 */
		QUANTITY_UPDATED: {}
	}
});
