export const ITEM_SELECTED = "SN_CATALOG_QUANTITY#ITEM_SELECTED";
export const QUANTITY_UPDATED = "SN_CATALOG_QUANTITY#QUANTITY_UPDATED";
export const actions = {
	[ITEM_SELECTED]: {
		private: true
	}
};
export const actionHandlers = {
	[ITEM_SELECTED]: ({ action, dispatch, properties, updateProperties }) => {
		const { payload = {} } = action;

		updateProperties({
			...properties,
			value: payload.value
		});
		dispatch(QUANTITY_UPDATED, {
			value: payload.value,
			displayValue: payload.displayValue
		});
	}
};
