import "@devsnc/sn-form-controls";
import { ITEM_SELECTED } from "./actions";
import { t } from "sn-translate";

export const view = (
	{ properties: { value, items, visible, label } },
	dispatch
) => {
	const QUANTITY_LABEL = label || t("Quantity");

	if (visible && items) {
		return (
			<div className="sn-catalog-quantity">
				<sn-record-choice
					value={value}
					choices={items}
					label={QUANTITY_LABEL}
					onValueChange={data => {
						dispatch(ITEM_SELECTED, data);
					}}
				/>
			</div>
		);
	}
};
